export default class FSPoint {
  public x = 0;
  public y = 0;

  constructor(x = 0, y = 0) {
    this.x = Math.floor(x);
    this.y = Math.floor(y);
  }

  public snap(s) {
    const x = Math.floor(this.x / s) * s;
    const y = Math.floor(this.y / s) * s;

    this.setPoint(x, y);
  }

  public setPoint(x: number, y: number){
    this.x = Math.floor(x);
    this.y = Math.floor(y);
  }

  public offset(pt = new FSPoint(), scale = -1): FSPoint{
    const x = this.x + (pt.x * scale);
    const y = this.y + (pt.y * scale);

    return new FSPoint(x, y);
  }

  public toDeg(num: number) {
    return Math.abs(Math.ceil(num * (180 / Math.PI)));
  }

  public to(pt: FSPoint) {
    const pts: FSPoint[] = [ this, pt ]
    const ret = {
      run: pts[1].x - pts[0].x,
      rise: pts[1].y - pts[0].y,
      slopeX: 1,
      slopeY: 1,
      max: 0,
      time: 0,
      dist: 0,
      vel: 0,
      hyp: 0
    }

    // ret.hyp = Math.hypot(ret.run, ret.rise);
    ret.hyp = Math.sqrt(Math.pow(ret.run, 2) + Math.pow(ret.rise, 2));
    ret.dist = Math.sqrt( Math.abs(ret.run) + Math.abs(ret.rise) );

    if (Math.abs(ret.run) > Math.abs(ret.rise)) {
      ret.max = ret.run;
      ret.slopeX = 1;
      ret.slopeY = ret.rise / ret.run;
    } else {
      ret.max = ret.rise;
      ret.slopeX = ret.run / ret.rise;
      ret.slopeY = 1;
    }

    return ret;

  }
}
