





import EventBus from '@/EventBus';
import { Component, Vue, Watch } from 'vue-property-decorator';
import FSPoint from '../intern/FSPoint';

@Component
export default class FSCursor extends Vue{
  $refs!: {
    cur: HTMLCanvasElement[];
  }

  private globalID;
  private pos = new FSPoint();
  private pos1 = new FSPoint();
  private el;
  // private pdn = false;

  @Watch('pos', {immediate: true, deep: true})
  onPosChange(val){
    if(this.el){
      // console.log(val)
      this.el.style.left = val.x + "px";
      this.el.style.top = val.y + "px";
    }
  }

  private draw(idx, pr = 0){
    const can = this.$refs.cur[idx];
    const ctx = can.getContext('2d') as CanvasRenderingContext2D;

    const p = Math.round(pr * 4);
    ctx.clearRect(0, 0, 20, 20);

    ctx.fillRect(0, 10, 21, 1);
    ctx.fillRect(10, 0, 1, 21);

    ctx.clearRect(8 - p, 8 - p, 5 + p * 2, 5 + p * 2);
    ctx.fillRect(10 - p, 10 - p, 1 + p * 2, 1 + p * 2);
    // ctx.fillRect(10 - p, 10 - p, 1 + p * 2, 1 + p * 2);
  }


  private test(e){
    this.pos = new FSPoint(e.offsetX - 10, e.offsetY - 10);
    // this.draw(0, e.pressure);
    // this.pos.setPoint(e.offsetX-10, e.offsetY-10);
  }

  destroyed(){
    EventBus.$off('pMv');
    EventBus.$off('pUp');
  }

  mounted(){
    for(const cur of this.$refs.cur){
      cur.width = 21;
      cur.height = 21;
    }

    this.draw(0);

    this.$nextTick(() => {
      this.el = document.getElementById('cursorContainer');
    });

    EventBus.$on('pDn', (e)=>{
      // this.pos.setPoint(e.offsetX-10, e.offsetY-10);
    });

    EventBus.$on('pUp', (e)=>{
      // this.pos = new FSPoint();
      // this.pos.setPoint(e.offsetX-10, e.offsetY-10);
      // console.log('up')
    });

    EventBus.$on('pMv', this.test)
      /*
      console.log('test', 40);
      if(!e.pressure){
        if(this.pdn){
          console.log('done', this.pos.x, this.pos.y);
          this.pdn = false;
        }
      } else {
        console.log(this.pos.x, this.pos.y);
        this.pdn = true;
      }
    });
      */
  }
}
